<script>
export default {
  data () {
    return {
      carts: [],
      total: []
    }
  },
  methods: {
    getCartsData () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.$http.get(url)
        .then(res => {
          console.log(res)
          this.carts = res.data.data.carts
          this.total = res.data.data
          console.log(this.total)
        })
    }
  },
  mounted () {
    this.getCartsData()
  }
}
</script>
